.blog{
    padding: 130px 0;
}
.post{
    border-radius: 5px;
    overflow: hidden;
    background: $white;
    transition: .3s ease;
    &-thumb{
        img{
            width: 100%;
        }
    }
    &-title{
        padding: 30px 25px;
        h5{
            a{
                font-weight: 400;
                line-height: 30px;
                color: #555555;
                text-decoration: none;
                transition: .3s ease;
            }
            margin-bottom: 0;
        }
    }
    &:hover{
        box-shadow: 0px 25px 25px 0px rgba(22, 25, 93, 0.07);
        .post-title{
            h5{
                a{
                    color: $secondary-color;
                }
            }
        }
    }
}