.audience{
    padding: 250px 0 200px;
    &-logo{
        margin-bottom: 70px;
        position: relative;
        .borders{
            position: absolute;
            @include size(230px);
            border-radius: 50%;
            border: 1px solid #e8eaed;
            top: 50%; left: 50%;
            transform: translate(-50%, -50%);
            &::before, &::after{
                position: absolute;
                content: '';
                border-radius: 50%;
                border: 1px solid #e8eaed;
                top: 50%; left: 50%;
                transform: translate(-50%, -50%);
                z-index: -1;
            }
            &::before{
                @include size(420px);
            }
            &::after{
                @include size(620px);
            }
        }
        img{
            border-radius: 50%;
            box-shadow: 0px 34px 46px 0px rgba(70, 56, 192, 0.16);
        }
        .icon{
            position: absolute;
            border-radius: 50%;
            &.hand{
                @include size(85px);
                background: $secondary-gradient;
                line-height: 85px;
                top: -100%; right: 18%;
                box-shadow: 0px 34px 26px 0px rgba(70, 172, 233, 0.17);
            }
            &.eye{
                @include size(85px);
                background: $primary-gradient;
                line-height: 85px;
                box-shadow: 0px 34px 26px 0px rgba(238, 85, 101, 0.14);
                top: -90%; left: 16%;
            }
            &.love-left{
                @include size(65px);
                background: $violate-gradient;
                line-height: 65px;
                top: 50%;
                left: -3%;
                box-shadow: 0px 34px 46px 0px rgba(70, 56, 192, 0.33);
                img{
                    width: 35px;
                    height: auto;
                }
            }
            &.plus{
                @include size(83px);
                background: $violate-gradient;
                line-height: 83px;
                box-shadow: 0px 34px 46px 0px rgba(70, 56, 192, 0.33);
                top: 25%; right: -5%;
            }
            &.love-right{
                @include size(65px);
                line-height: 65px;
                background: $primary-gradient;
                box-shadow: 0px 34px 26px 0px rgba(238, 85, 101, 0.14);
                top: -30%;
                right: -35%;
                img{
                    width: 35px;
                    transform: rotate(25deg);
                }
            }
            &.chat{
                @include size(55px);
                line-height: 55px;
                right: -45%;
                top: 100%;
                background: $secondary-gradient;
                box-shadow: 0px 34px 26px 0px rgba(70, 172, 233, 0.17);
                img{
                    width: 30px;
                }
            }
            &.message{
                @include size(65px);
                line-height: 65px;
                background: $secondary-gradient;
                box-shadow: 0px 34px 26px 0px rgba(70, 172, 233, 0.17);
                left: -35%;
                top: -35%;
                img{
                    width: 35px;
                    height: auto;
                }
            }
            &.user{
                @include size(55px);
                line-height: 55px;
                top: 110%; left: -45%;
                background: $primary-gradient;
                box-shadow: 0px 34px 26px 0px rgba(238, 85, 101, 0.14);
            }
        }
    }
    &-content{
        position: relative;
        z-index: 2;
        h2{
            margin-bottom: 35px;
        }
    }
}