.footer{
    padding: 170px 0 140px;
    background: url('../images/backgrounds/footer-bg.png');
    background-size: cover;
    background-position: top center;
    &-content{
        img{
            display: block;
            margin: 0 auto 45px;
        }
        ul.links{
            padding-bottom: 50px;
            border-width: 0 0 2px 0; border-style: solid;
            border-color: rgba($color: $white, $alpha: .10);
            margin-bottom: 25px;
            li.list-inline-item{
                a{
                    color: $white;
                }
                &:not(:last-child){
                    margin-right: 40px;
                }
            }
        }
        .copyright-text{
            p{
                color: $white;
            }
        }
    }
}